<template>
  <div class="text-center font-login">
    <div>
      <img class="img-logo" alt="ZS logo" src="@/assets/zs-1.png" />
      <br />
      <img class="img-str" alt="ZS logo" src="@/assets/zsznzz.png" />
    </div>

    <b-form v-on:submit="postData" v-on:reset="toIndex" class="pt-2 col-md-4 offset-md-4">
      <b-alert variant="danger" dismissible fade v-bind:show="errShow">用户名或密码错误！</b-alert>

      <b-input-group class="py-3">
        <b-input-group-prepend is-text>
          <span class="font-login">用户名:</span>
        </b-input-group-prepend>
        <b-form-input class="font-login" v-model="form.name"></b-form-input>
      </b-input-group>

      <!-- <b-input-group prepend="密码:" class="py-2"> -->
      <b-input-group class="py-3">
        <b-input-group-prepend is-text>
          <span class="font-login">密码:</span>
        </b-input-group-prepend>
        <b-form-input class="font-login" type="password" v-model="form.pwd"></b-form-input>
      </b-input-group>

      <div v-if="isLoading" class="py-3 text-info">
          <b-spinner ></b-spinner>
          <span > 登录中...</span>
      </div>

      <div v-else class="py-3">
        <b-button class="m-1 font-login" variant="outline-info" type="submit">登录</b-button>
        <b-button class="m-1 font-login" variant="outline-secondary" type="reset">主页</b-button>
      </div>
    </b-form>
  </div>
</template>

<script>
//引入BsV的component
import {
  // BRow,
  // BCol,
  // // BFormSelect,
  BButton,
  BSpinner,
  BAlert,
  BForm,
  // BCardGroup,
  // BCardFooter,
  // BFormGroup,
  BFormInput,
  BInputGroup,
  BInputGroupPrepend,
  // BInputGroupAppend,
  // BFormRadioGroup
} from "bootstrap-vue";

import { asyFetch, getServUrl } from "@/utility/util";

import { mapState, mapMutations } from "vuex";
// import { asyFetch } from "@/utility/util";

export default {
  name: "Login",
  data() {
    return {
      form: {
        name: "xx",
        pwd: "",
      },
      // plus: {
      //   tblName: "data_raw",
      //   customer_id: 0
      // },
      // timer: null, //定义计时器名称
      // timestr: "",
      errShow: false,
      isLoading: false,
      res: { reged: false },
    };
  },
  computed: {
    ...mapState({
      isLogin: (state) => state.isLogin,
      // actIndex:state=>state.navbar.index,
      // isBriefContent:state=>state.isBriefContent,
    }),
  },

  methods: {
    postData: async function (evt) {
      evt.preventDefault();
      console.log("post");
      this.errShow = false;
      // console.log(process.env.NODE_ENV);
      if (!this.checkInput()) {
        this.errShow = true;
        this.isLoading = false;
        return this.resetForm();
      } else {
        this.isLoading = true;
      }

      const opt = { method: "POST", body: JSON.stringify(this.form) };
      const url = getServUrl() + "login";
      const res = await asyFetch(url, opt);

      if (res.ok) {
        this.res = Object.assign({}, this.res, res.cont);
      }

      if (this.res.reged) {
        this.updateIsLogin(true);
        this.$router.push({ name: "admin" });
      } else {
        this.errShow = true;
        this.resetForm();
      }
      this.isLoading = false;
    },
    toIndex(evt) {
      evt.preventDefault();
      console.log("toIndex");
      this.$router.push("index");
    },
    checkInput() {
      return true;
    },
    resetForm() {
      this.res.reged = false;
      this.form = Object.assign({}, { name: "", pwd: "" });
    },
    ...mapMutations(["updateIsLogin"]),
  },
  components: {
    // BRow,
    // BCol,
    BButton,
    BSpinner,
    BAlert,
    BForm,
    // BCardGroup,
    // BCardFooter,
    // BFormGroup,
    BFormInput,
    BInputGroup,
    BInputGroupPrepend,
    // BInputGroupAppend,
    // BFormRadioGroup
  },
};
</script>

